





















export default {
	data() {
		return {
			title: 'picker',
			array: ['中国', '美国', '巴西', '日本'],
			index: 0,
			time: '12:01',
			date: '2018-9-1',
			add:"深圳"
		};
	},
	methods: {
		bindPickerChange(e) {
			this.index = e.target.value;
		},
		bindTimeChange(e) {
			this.time = e.target.value;
		},
		bindDateChange(e) {
			this.date = e.target.value;
		},
		bindAddChange(e){
			this.add = e.target.value;
		}
	}
};
